import React, { Fragment } from "react";
import { AccessControlTypes, Trigger } from "../../../types";
import { ModalContent, ModalOpenButton, useModalContext } from "../Modal";
import { Form, FormContentBase } from "../Form/Form";
import { Button } from "antd";

export default function TriggerModal() {
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/trigger", "create", {} as Trigger);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator", "Create"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
        <Button children="Create New Trigger" />
      </ModalOpenButton>
      <ModalContent title="Create new trigger" onClickOk={handleOk} formName="create_trigger_form">
        <Form schemaName="trigger">
          <FormContentBase
            name="create_trigger_form"
            form={form}
            preserve={false}
          />
        </Form>
      </ModalContent>
    </Fragment>
  );
}
